<template>
  <div class="horizontal-wrapper">
    <div
      v-bind:class="
        'side-menu ' +
        (mq.tablet || mq.other
          ? ''
          : 'side-menu-mobile ' +
            (sideMenuShown
              ? 'side-menu-mobile-shown'
              : 'side-menu-mobile-hidden'))
      "
    >
      <div class="side-menu-buttons">
        <button class="button is-light is-medium" @click="goTo('/')">
          <span class="material-icons-outlined"> home </span>
        </button>
        <button class="button is-light is-medium" @click="goTo('/history')">
          <span class="material-icons-outlined"> work_history </span>
        </button>

        <button class="button is-light is-medium" @click="goTo('/cart')">
          <span class="material-icons-outlined"> shopping_cart </span>
        </button>

        <div class="dropdown is-hoverable">
          <div class="dropdown-trigger ">
            <button class="button is-light is-medium" aria-haspopup="true" aria-controls="dropdown-menu4">
              <span class="material-icons-outlined"> settings </span>
            </button>
          </div>
          <div class="dropdown-menu" id="dropdown-menu4" role="menu">
            <div class="dropdown-content">
              <div class="dropdown-item">  
                <div class="control has-icons-left languages bottom">
                  <div class="select refresh_button">
                    <select id="select" class="refresh_button" @change="change_language($event)">
                      <option value="en-EN">{{$t("language.en")}}</option>
                      <option value="fr-FR">{{$t("language.fr")}}</option>
                      <option value="de-DE">{{$t("language.de")}}</option>
                      <option value="it-IT">{{$t("language.it")}}</option>
                    </select>
                  </div>
                  <span class="icon is-left">
                    <span class="material-icons-outlined"> language </span>
                  </span>
              </div>
              </div>
              <div class="dropdown-item">  
                  <div class="button refresh_button" @click="syncProduct()"><span class="material-icons"> refresh </span></div>
             </div>
            </div>
          </div>
        </div>

      </div>
      
      <slot name="menu" />
    </div>

    <div
      v-if="!mq.tablet && sideMenuShown"
      class="backdrop"
      @click="closeSideMenu()"
    />

    <div class="vertical-wrapper">
      <div class="header">
        <slot name="header" />
      </div>

      <div class="content">
        <slot name="content" />
      </div>
    </div>
  </div>
</template>


<script setup>
import { useMq } from "vue3-mq";

const mq = useMq()
</script>


<script>
import { mapState } from "vuex";

export default {
  inject: ["mq"],
  name: "AppLayout",

  computed: mapState({
    sideMenuShown: (state) => state.sideMenuShown,
    user: (state) => state.user,
  }),
  mounted() {
    this.$root.$i18n.locale.substring(0,2)
    this.$store.dispatch("category.fetchCategory",{"locale" : this.$i18n.locale.substring(0,2),"force" : false})
    var index = 0
    switch(navigator.language){
      case"fr-FR":index = 1;break
      case"de-DE":index = 2;break
      case"it-IT":index = 3;break
      default:index = 0;break
    }
    document.getElementById("select").selectedIndex = index
  },

  methods: {
    closeSideMenu() {
      this.$store.commit("sideMenu.close")
    },

    goTo(route) {
      this.$router.push(route)
      this.closeSideMenu()
    },
    logout() {
      this.$store.dispatch("user-auth.getUser", {
        id: -1,
        user: "",
        pwd: "",
      });
      this.goTo("/")
    },
    syncProduct(){
      this.$store.dispatch("catalog.syncProductsIfNeeded")
    },
    change_language(event){
       this.$root.$i18n.locale = event.target.value.substring(0,2)
       this.$store.dispatch("category.fetchCategory",{"locale" : this.$i18n.locale,"force" : true})
    }
  },
};
</script>


<style scoped>
.horizontal-wrapper {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: stretch;
}

.side-menu {
  min-width: 25%;
  background-color: #d2ffc3;
}

.side-menu-mobile {
  position: absolute;
  top: 0;
  left: 0;

  height: 100%;
  border-right: solid black 2px;

  z-index: 4;

  transition: transform 0.2s ease-in;
}

.side-menu-mobile-shown {
  transform: translateX(0);
}

.side-menu-mobile-hidden {
  transform: translateX(-100%);
}

.side-menu-buttons {
  height: 50px;
  margin-top: 10px;
  margin-bottom: 10px;

  display: flex;
  justify-content: space-around;
  align-items: center;
}

.backdrop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  z-index: 3;

  background-color: rgba(200, 200, 200, 0.65);

  animation: backdrop-fade 0.2s ease-in-out;
}

@keyframes backdrop-fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.vertical-wrapper {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
}

.header {
  height: 50px;
  padding: 10px;
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  gap: 8px;
}

.content {
  flex-grow: 1;
  padding: 10px;
  overflow: auto;
}
.refresh_button{
  width: 100% !important;
}

</style>
